.cat-leg {
  padding: ($leg-width - 2 * $stroke-width) / 2;
  margin-top: -$leg-width / 2;
  margin-left: $inner-width;
  position: absolute;
  border-radius: 10em;
  background-color: $stroke-color;
  border: $stroke-color $stroke-width solid;
  
  & > div {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background-color: $cat-body-color;
    border-radius: 10em;
  
    &:before,
    &:after {
      content: '';
      padding: $stroke-width / 2;
      background-color: $stroke-color;
      position: absolute;
      top: 50%;
      margin-top: -$stroke-width / 2;
      border-radius: 10em;
    }
    
    &:before {
      right: 100%;
    }
    
    &:after {
      left: 100%;
    }
  }
}

.cat-loader-container {
  font-size: $cat-size;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-family: monospace;
  overflow: hidden;
  text-align: initial;
  min-width: 50em;
}

* {
  box-sizing: border-box;
}

div {
  display: inline-block;
}
</style>


